@import 'base';
//@import '~bootstrap/scss/bootstrap.scss';

// Some best-practice CSS that's useful for most apps
// Just remove them if they're not what you want
html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h3 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  .label {
    margin-right: 5px;
    margin-bottom: 5px;
  }
}

button.pull-right {
  margin-top: 20px;
}


